(function() {
  this.App.run(["$rootScope", "APP_NAME", function($rootScope, APP_NAME) {
    return $rootScope.app_name = APP_NAME;
  }]).run(["$rootScope", function($rootScope) {
    return $rootScope.$on('$viewContentLoaded', function() {
      return window.scrollTo(0, 0);
    });
  }]).run(["$templateCache", function($templateCache) {
    return $templateCache.put('directives/toast/toast.html', '<div class="{{toastClass}} {{toastType}}" ng-click="tapToast()">\n  <div ng-switch on="allowHtml">\n    <div ng-switch-default ng-if="title" class="{{titleClass}}" aria-label="{{title | translate}}">{{title | translate:extraData}}</div>\n    <div ng-switch-default class="{{messageClass}}" aria-label="{{message | translate:extraData}}">{{message | translate:extraData}}</div>\n    <div ng-switch-when="true" ng-if="title" class="{{titleClass}}" ng-bind-html="title | translate:extraData"></div>\n    <div ng-switch-when="true" class="{{messageClass}}" ng-bind-html="message | translate:extraData"></div>\n  </div>\n  <progress-bar ng-if="progressBar"></progress-bar>\n</div>');
  }]).run(["toastr", "$location", function(toastr, $location) {
    var flash, message;
    if (flash = $location.search().flash) {
      message = JSON.parse(flash);
      toastr[message.type](message.msg, _.capitalize(message.type), {
        timeout: 10000
      });
      return $location.search('flash', null);
    }
  }]);

}).call(this);

