(function() {
  this.App.controller('ImpacController', ["action", "$rootScope", "$stateParams", "$state", "MnoeDashboardTemplates", "ImpacDashboardsSvc", function(action, $rootScope, $stateParams, $state, MnoeDashboardTemplates, ImpacDashboardsSvc) {
    'ngInject';
    var vm;
    vm = this;
    vm.createTemplate = function() {
      vm.showImpac = false;
      return ImpacDashboardsSvc.load().then(function(config) {
        var e, modal, s;
        vm.currentDashboard = config.currentDashboard;
        e = document.getElementById('module__dashboard-selector');
        s = angular.element(e).scope();
        modal = s.createDashboardModal;
        modal.open();
        return modal.instance.result.then(function(closed) {
          return vm.showImpac = true;
        }, function(dismissed) {
          return $state.go('dashboard.dashboard-templates');
        });
      });
    };
    vm.editTemplate = function() {
      vm.showImpac = true;
      vm.dashboardLoaded = false;
      $rootScope.$watch(function() {
        return vm.dashboardLoaded;
      }, function(loaded) {
        var dashboardId;
        if (loaded) {
          dashboardId = parseInt($stateParams.dashboardId);
          ImpacDashboardsSvc.setCurrentDashboard(dashboardId);
          return vm.currentDashboard = ImpacDashboardsSvc.getCurrentDashboard();
        }
      });
      if (!vm.callbackRegistered) {
        ImpacDashboardsSvc.dashboardChanged().then(null, null, function() {
          return vm.dashboardLoaded = true;
        });
        return vm.callbackRegistered = true;
      }
    };
    switch (action.value) {
      case 'create':
        vm.createTemplate();
        break;
      case 'edit':
        vm.editTemplate();
    }
    vm.toggleTemplatePublished = function(id) {
      return MnoeDashboardTemplates.toggleTemplatePublished(id);
    };
  }]);

}).call(this);

