(function() {
  this.App.service('MnoeUsers', ["$q", "$log", "MnoeAdminApiSvc", "MnoeObservables", "OBS_KEYS", function($q, $log, MnoeAdminApiSvc, MnoeObservables, OBS_KEYS) {
    var _getStaffs, _self;
    _self = this;
    this.list = function(limit, offset, sort) {
      var promise;
      return promise = MnoeAdminApiSvc.all('users').getList({
        order_by: sort,
        limit: limit,
        offset: offset
      }).then(function(response) {
        MnoeObservables.notifyObservers(OBS_KEYS.userChanged, response);
        return response;
      });
    };
    this.staffs = function(limit, offset, sort, params) {
      if (params == null) {
        params = {};
      }
      if (!params['where[admin_role.in][]']) {
        params['where[admin_role.not]'] = '';
      }
      return _getStaffs(limit, offset, sort, params);
    };
    this.search = function(terms) {
      return MnoeAdminApiSvc.all('users').getList({
        terms: terms
      });
    };
    this.get = function(id) {
      return MnoeAdminApiSvc.one('users', id).get();
    };
    this.count = function() {
      return MnoeAdminApiSvc.all('users').customGET('count');
    };
    this.addUser = function(organization, user) {
      return MnoeAdminApiSvc.one('organizations', organization.id).all('/users').post({
        user: user
      });
    };
    this.addStaff = function(user) {
      var promise;
      return promise = MnoeAdminApiSvc.all('users').post({
        user: user
      }).then(function(response) {
        MnoeObservables.notifyObservers(OBS_KEYS.staffAdded, promise);
        return response;
      });
    };
    this.updateStaff = function(user) {
      var promise;
      return promise = MnoeAdminApiSvc.one('users', user.id).patch({
        user: user
      }).then(function(response) {
        MnoeObservables.notifyObservers(OBS_KEYS.staffChanged, promise);
        return response;
      });
    };
    this.removeStaff = function(id) {
      var promise;
      return promise = MnoeAdminApiSvc.one('users', id).patch({
        admin_role: null
      }).then(function(response) {
        return MnoeObservables.notifyObservers(OBS_KEYS.staffChanged, promise);
      }, function(error) {
        $log.error('Error while deleting user', error);
        return toastr.error('mnoe_admin_panel.dashboard.staff.modal.remove_staff.toastr_error');
      });
    };
    this.inviteUser = function(organization, user) {
      return MnoeAdminApiSvc.one('organizations', organization.id).one('users', user.id).doPOST({}, '/invites');
    };
    this.sendSignupEmail = function(email) {
      return MnoeAdminApiSvc.all('/users').doPOST({
        user: {
          email: email
        }
      }, 'signup_email');
    };
    _getStaffs = function(limit, offset, sort, params) {
      if (params == null) {
        params = {};
      }
      params["order_by"] = sort;
      params["limit"] = limit;
      params["offset"] = offset;
      return MnoeAdminApiSvc.all("users").getList(params);
    };
    return this;
  }]);

}).call(this);

