(function() {
  this.App.controller('ConnectAppController', ["$stateParams", "$window", "$uibModal", "MnoeOrganizations", "MnoAppsInstances", function($stateParams, $window, $uibModal, MnoeOrganizations, MnoAppsInstances) {
    'ngInject';
    var vm;
    vm = this;
    vm.mnoAppInstances = MnoAppsInstances;
    vm.orgId = $stateParams.orgId;
    vm.isLoading = true;
    MnoeOrganizations.get($stateParams.orgId).then(function(response) {
      return vm.organization = response.data;
    })["finally"](function() {
      return vm.isLoading = false;
    });
    vm.connectAppInstance = function(app) {
      var modalInfo;
      switch (app.nid) {
        case "xero":
          modalInfo = {
            template: "app/views/customers/app-connect-modal/app-connect-modal-xero.html",
            controller: 'ConnectXeroModalCtrl'
          };
          break;
        case "myob":
          modalInfo = {
            template: "app/views/customers/app-connect-modal/app-connect-modal-myob.html",
            controller: 'ConnectMyobModalCtrl'
          };
          break;
        default:
          $window.location.href = MnoAppsInstances.oAuthConnectPath(app);
          return;
      }
      return $uibModal.open({
        templateUrl: modalInfo.template,
        controller: modalInfo.controller,
        controllerAs: 'vm',
        resolve: {
          app: function() {
            return app;
          }
        }
      });
    };
  }]);

}).call(this);

(function() {
  this.App.controller('CreateStep1Controller', ["$scope", "$document", "$state", "toastr", "MnoeAdminConfig", "MnoeOrganizations", "MnoeMarketplace", "MnoErrorsHandler", function($scope, $document, $state, toastr, MnoeAdminConfig, MnoeOrganizations, MnoeMarketplace, MnoErrorsHandler) {
    'ngInject';
    var vm;
    vm = this;
    vm.organization = {};
    vm.appSearch = "";
    vm.toggleApp = function(app) {
      return app.checked = !app.checked;
    };
    vm.submitOrganisation = function() {
      var form;
      if (vm.form.$invalid) {
        if (!MnoErrorsHandler.onlyServerError(vm.form)) {
          form = angular.element(document.getElementById('org-form'));
          $document.scrollToElementAnimated(form);
          return;
        }
      }
      vm.isLoading = true;
      MnoErrorsHandler.resetErrors(vm.form);
      if (MnoeAdminConfig.isAppManagementEnabled()) {
        vm.organization.app_nids = _.map(_.filter(vm.marketplace.apps, {
          checked: true
        }), 'nid');
      }
      return MnoeOrganizations.create(vm.organization).then(function(response) {
        toastr.success('mnoe_admin_panel.dashboard.customers.create_customer.toastr_success', {
          extraData: {
            organization_name: vm.organization.name
          }
        });
        response = response.data.plain();
        if (_.isEmpty(response.organization.active_apps)) {
          return $state.go('dashboard.customers.organization', {
            orgId: response.organization.id
          });
        } else {
          return $state.go('dashboard.customers.connect-app', {
            orgId: response.organization.id
          });
        }
      }, function(error) {
        $document.scrollTopAnimated(0);
        toastr.error('mnoe_admin_panel.dashboard.customers.create_customer.toastr_error', {
          extraData: {
            organization_name: vm.organization.name
          }
        });
        return MnoErrorsHandler.processServerError(error, vm.form);
      })["finally"](function() {
        return vm.isLoading = false;
      });
    };
    if (MnoeAdminConfig.isAppManagementEnabled()) {
      MnoeMarketplace.getApps().then(function(response) {
        return vm.marketplace = angular.copy(response.data);
      });
    }
  }]);

}).call(this);

