(function() {
  this.App.controller('AddAppModalCtrl', ["$scope", "$uibModalInstance", "$window", "MnoeMarketplace", "MnoeOrganizations", "organization", "toastr", function($scope, $uibModalInstance, $window, MnoeMarketplace, MnoeOrganizations, organization, toastr) {
    'ngInject';
    var vm;
    vm = this;
    vm.modal_height = ($window.innerHeight - 200) + "px";
    vm.selectedApp = {};
    vm.organization = angular.copy(organization);
    vm.loading = {
      modal: true,
      apps: false
    };
    vm.displayError = false;
    MnoeMarketplace.getApps().then(function(response) {
      var activeAppNids;
      vm.marketplace = angular.copy(response.data.plain());
      activeAppNids = _.map(organization.active_apps, 'nid');
      return vm.marketplace.filtered_apps = _.filter(vm.marketplace.apps, function(app) {
        if (!_.includes(activeAppNids, app.nid)) {
          return app;
        }
      });
    })["finally"](function() {
      return vm.loading.modal = false;
    });
    vm.toggleApp = function(app) {
      return app.checked = !app.checked;
    };
    vm.closeError = function() {
      return vm.displayError = false;
    };
    vm.addApps = function() {
      vm.loading.apps = true;
      vm.appNidsToAdd = _.map(_.filter(vm.marketplace.apps, {
        checked: true
      }), 'nid');
      vm.organization.app_nids = _.union(vm.appNidsToAdd, _.map(organization.active_apps, 'nid'));
      if (vm.appNidsToAdd.length === 0) {
        vm.closeModal();
        return;
      }
      return MnoeOrganizations.update(vm.organization).then(function(success) {
        var active_apps_nids, nb_active_apps, nb_apps_to_add;
        nb_active_apps = success.data.plain().organization.active_apps.length;
        nb_apps_to_add = vm.organization.app_nids.length;
        vm.organization = success.data.plain().organization;
        if (nb_active_apps === nb_apps_to_add) {
          return vm.closeModal();
        } else {
          vm.displayError = true;
          active_apps_nids = typeof active_apps !== "undefined" && active_apps !== null ? _.map(active_apps, 'nid') : [];
          _.map(_.intersection(vm.appNidsToAdd, active_apps_nids), function(app_nid) {
            return _.remove(vm.marketplace.filtered_apps, {
              nid: app_nid
            });
          });
          vm.arrayOfApps = _.map(_.difference(vm.appNidsToAdd, active_apps_nids), function(app_nid) {
            return _.find(vm.marketplace.filtered_apps, {
              nid: app_nid
            }).name;
          });
          return vm.ListOfApps = vm.arrayOfApps.join(', ');
        }
      }, function(error) {
        return toastr.error("mnoe_admin_panel.dashboard.organization.add_app.toastr_error", {
          preventDuplicates: false
        });
      })["finally"](function() {
        return vm.loading.apps = false;
      });
    };
    vm.closeModal = function() {
      return $uibModalInstance.close(vm.organization);
    };
  }]);

}).call(this);

